import React from 'react';
import { Link } from 'react-router-dom';

function priority(prior) {
	if (prior === 'Low' || prior === 'low') {
		return 'LOW';
	} else if (prior === 'Medium' || prior === 'medium') {
		return 'MEDIUM';
	} else if (prior === 'High' || prior === 'high') {
		return 'HIGH';
	}
}

function ToDoItem(props) {
	const completedStyle = {
		fontStyle: 'italic',
		color: '#cdcdcd',
		textDecoration: 'line-through'
	};

	return (
		<div className="todo-item">
			<input type="checkbox" checked={props.item.completed} onChange={() => props.handleChange(props.item._id)} />
			
			<span className={priority(props.item.task_priority)}>
				<p style={props.item.completed ? completedStyle : {}}>{props.item.task}</p>
				<p style={props.item.completed ? completedStyle : {}}>{props.item.responsibility}</p>
				<p style={props.item.completed ? completedStyle : {}}>{props.item.task_priority}</p>
			</span>

			<span>
				<Link to={'/edit/' + props.item._id}>Edit</Link>
			</span>

			<span>
				<button
					className="btn btn-delete"
					onClick={() => {
						if (window.confirm(`Are you sure you want to delete this task :  ${props.item.task}?`)) {
							return props.deleteTask(props.item._id);
						}
					}}
				>
					Delete
				</button>
			</span>
		</div>
	);
}

export default ToDoItem;
