import React from 'react';
import './App.css';
import ToDoItem from './components/to-do-item.js';
import NewTask from './components/newtask';
import Axios from 'axios';

class Main extends React.Component {
	constructor() {
		super();
		this.state = {
			todos: [],
			isHidden: true
		};
	}

	fetchAll = () => {
		Axios.get('http://localhost:4000/todos/')
			.then((res) => {
				this.setState({ todos: res.data });
			})
			.catch((err) => {
				console.error('Unable To Retrieve Data. Error: ' + err);
			});
	};

	componentDidMount() {
		this.fetchAll();
	}

	handleChange = (id) => {
		this.setState((prevState) => {
			const updatetodo = prevState.todos.map((item) => {
				if (item._id === id) {
					//update database

					const obj = {
						completed: !item.completed
					};
					Axios.post('http://localhost:4000/todos/completed/' + id, obj).then((res) => console.log(res.data));
					item.completed = !item.completed;
				}
				return item;
			});
			return {
				todos: updatetodo
			};
		});
	};

	deleteTask = (id) => {
		Axios.delete('http://localhost:4000/todos/delete/' + id).then((res) => {
			if (res.status === 200) {
				console.log(res.data);
				this.fetchAll();
			}
		});
	};

	toggleHidden = () => {
		this.setState({
			isHidden: !this.state.isHidden
		});
	};

	render() {
		const todoItems = this.state.todos.map((item) => (
			<ToDoItem key={item._id} item={item} handleChange={this.handleChange} deleteTask={this.deleteTask} />
		));
		return (
			<div>
				<div className="todo-list">
					<h2>Todos List</h2>
					{this.state.todos.length <= 0 ? <h3>No Tasks Found!</h3> : todoItems}
				</div>
				<div className="form">
					<button type="button" className="btn" onClick={this.toggleHidden}>
						Create New
					</button>
					{!this.state.isHidden && <NewTask fetchAll={this.fetchAll} />}
				</div>
			</div>
		);
	}
}

export default Main;
